  /* App.css */
  /* Base Styles */
  * {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
  }

  body {
    font-family: 'Arial', sans-serif;
    overflow-x: hidden;
  }

  /* Video Background */
  .video-background {
    position: fixed;
    right: 0;
    bottom: 0;
    min-width: 100%;
    min-height: 100%;
    z-index: -1;
  }

  /* Content Overlay */
  .content-overlay {
    position: relative;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 20px;
    background: rgba(0, 0, 0, 0.7);
    text-align: center;
  }

  /* Logo Styles */
  .logo {
    width: 150px;
    margin-bottom: 2rem;
    border-radius: 50%;
    border: 3px solid #fff;
  }

  /* Text Styles */
  .title {
    color: #fff;
    font-size: 2.5rem;
    margin-bottom: 1.5rem;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
  }

  .description {
    color: #fff;
    font-size: 1.2rem;
    margin-bottom: 3rem;
    max-width: 600px;
    line-height: 1.6;
  }

  /* Button Container */
  .button-container {
    width: 100%;
    max-width: 400px;
    display: flex;
    flex-direction: column;
    gap: 15px;
  }

  /* Action Buttons */
  .action-button {
    display: block;
    width: 100%;
    padding: 15px 30px;
    font-size: 1.1rem;
    font-weight: bold;
    color: white;
    text-decoration: none;
    border: none;
    border-radius: 25px;
    cursor: pointer;
    transition: all 0.3s ease;
  }

  .action-button:hover {
    transform: scale(1.05);
    filter: brightness(1.1);
  }

  /* Platform-specific Button Colors */
  .twitter-button {
    background: #1DA1F2;
  }

  .dexscreener-button {
    background: #FF6B00;
  }

  .telegram-button {
    background: #0088CC;
  }

  /* Animations */
  @keyframes pulse {
    0% { transform: scale(1); }
    50% { transform: scale(1.05); }
    100% { transform: scale(1); }
  }

  .animate-pulse {
    animation: pulse 2s infinite;
  }

  /* Mobile Responsiveness */
  @media (max-width: 768px) {
    .title {
      font-size: 2rem;
    }

    .description {
      font-size: 1rem;
      margin-bottom: 2rem;
    }

    .logo {
      width: 120px;
    }

    .action-button {
      font-size: 1rem;
      padding: 12px 25px;
    }
  }

  @media (max-width: 480px) {
    .title {
      font-size: 1.75rem;
    }

    .description {
      font-size: 0.9rem;
    }

    .content-overlay {
      padding: 15px;
    }
  }